

<template>
  <img alt="Vue logo" src="./assets/logo.png" @click="getFunc" />
  <span v-if="rcvData.data">{{ rcvData.data }}</span>
  <HelloWorld msg="Welcome to Your Vue.js App" />
</template>

<script>
import HelloWorld from "./components/HelloWorld.vue";
import axios from "axios";

export default {
  name: "App",
  data() {
    return {
      rcvData: {},
      timer: null,
    };
  },
  mounted() {
    this.timer = setInterval(() => {
      axios("/api/v1/data/heart_rate/latest", {
        methods: "GET",
        headers: {
          Authorization: `Bearer 781b6257-e81a-41f9-bf32-2fac35b2de92`,
        },
      }).then((response) => (this.rcvData = response));
      console.log('a');
    }, 1000);
  },

  beforeUnmount() {
    clearInterval(this.timer);
  },
  components: {
    HelloWorld,
  },
};
</script>

